import Toast from '@/wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			showPass: 'unapproval',
			pendingLists: [],
			active: 0,
			show: false,
			columns: ['一班', '二班', '三班'],
		}
	},
	methods: {
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 切换审批状态
		change(val) {
			this.showPass = val
		},
		// 获取待分配的学生列表
		pendingList() {
			this.$minApi.pendingList({}).then(data => {
				this.pendingLists = data.data
				console.log(this.pendingLists[0].studentAccount)
			})
		},
		// 选中mtab
		onChange(event) {
			console.log(event)
		},
		// 通过
		adopt() {
			Toast("通过")
		},
		// 不通过
		notPass() {
			Toast("不通过")
		},
		// 未分配
		// picker选择器
		onConfirm(event) {
			const {
				picker,
				value,
				index
			} = event.detail;
			console.log((`当前值：${value}, 当前索引：${index}`));
			this.show = false;
			Toast("加入班级成功！")
		},
		onCancel() {
			this.show = false;
		},
		// 显示选择班级弹框
		changeRanking() {
			this.show = true;
		},

	},
	created() {
		this.pendingList()
	}

}
